import { Injectable } from '@angular/core';

import { Http, Headers, RequestOptions } from '@angular/http';
import 'rxjs/add/operator/map';
import 'rxjs/add/observable/of';

@Injectable()
export class ProductService {
	// API_URL = 'http://172.24.62.104/link.eddmi.com/index.php/takeaway/';
	API_URL = 'https://link.eddmi.com/takeaway/';
	ENTITY_ID = 227;
	fixedMenu: any;
	dailyMenu: any;
	constructor(public http: Http) {
		// console.log('Hello ProductService Provider');
	}

	getFixed(local: string) : Promise<any> {
		return new Promise((resolve, reject) => {
			var url = this.API_URL + 'product/fixed?local=' + local;
			let _headers = new Headers({ 'Authorization': this.ENTITY_ID });
			let _options = new RequestOptions({headers: _headers});
			this.http.get(url, _options)
				.map(res => res.json())
				.subscribe(data => {
					this.fixedMenu = this.organizeByCategory(data.data.products);
					resolve(this.fixedMenu);
				}, error => {
					// console.log(error);
					reject(error);
				});
		});
	}  

	getDaily(local: string) : Promise<any> {
		return new Promise((resolve, reject) => {
			var url = this.API_URL + 'product/daily?local=' + local;
			let _headers = new Headers({ 'Authorization': this.ENTITY_ID });
			let _options = new RequestOptions({headers: _headers});
			this.http.get(url, _options)
				.map(res => res.json())
				.subscribe(data => {
					this.dailyMenu = this.organizeByCategory(data.data.products);
					resolve(this.dailyMenu);
				}, error => {
					// console.log(error);
					reject(error);
				});
		});
	}  

	organizeByCategory(products: any) {
		let auxCategory: string = '';
		let organized: any = [];
		products.forEach((product: any) => {
			if(!(organized.find((p: any) => p.name === product.category))) {
				auxCategory = product.category;
				let group = {
					name: auxCategory,
					items: products.filter((p: any) => p.category === auxCategory)
				}
				organized.push(group);
			}
		})
		return organized;
	}
	
}


