import { Component, ViewChild } from '@angular/core';

import { IonicPage, AlertController, App, List, ModalController, NavController, ToastController, Platform, LoadingController, Refresher } from 'ionic-angular';
import { Storage } from '@ionic/storage';

/*
	To learn how to use third party libs in an
	Ionic app check out our docs here: http://ionicframework.com/docs/v2/resources/third-party-libs/
*/
// import moment from 'moment';

import { ProductService } from '../../providers/menu-service';
import { UserService } from '../../providers/user-service';
import { SettingsService } from '../../providers/settings-service';
import { Cart, CartService } from '../../providers/cart-service';
import { OrderService } from '../../providers/order-service';

@IonicPage()
@Component({
	selector: 'page-menu',
	templateUrl: 'menu.html',
	providers: [ProductService, CartService, OrderService, SettingsService]
})
export class MenuPage {
	// the list is a child of the menu page
	// @ViewChild('scheduleList') gets a reference to the list
	// with the variable #scheduleList, `read: List` tells it to return
	// the List and not a reference to the element
	@ViewChild('scheduleList', { read: List }) scheduleList: List;

	queryText = '';
	segment = 'daily';
	loading: any;
	excludeCategories: any = [];
	shownSessions: any = [];
	groups: any = [];
	confDate: string;

	fixedMenu: any = [];
	dailyMenu: any = [];
	cart: any;
	
	platform: string;

	local: string;

	constructor(
		public alertCtrl: AlertController,
		public app: App,
		public loadingCtrl: LoadingController,
		public modalCtrl: ModalController,
		public navCtrl: NavController,
		public toastCtrl: ToastController,
		public productService: ProductService,
		public cartService: CartService,
		public orderService: OrderService,
		public user: UserService,
		public plt: Platform,
		public storage: Storage,
		public settingsService: SettingsService
	) {
		this.cart = new Cart();
		this.cartService.get().then((cart) => {
			this.cart = cart;
		})
	}

	ionViewDidLoad() {
		this.app.setTitle('menu');
		// this.cartService.set(this.cart);

		this.user.hasLoggedIn().then((data) => {
			// console.log("user", data);
		});

		if(this.plt.is('core')) {
			this.platform = "desktop"
		}
		else if(this.plt.is('ios')) {
			this.platform = "ios";
		}
		else {
			this.platform = "android";
		}

		let alert = this.alertCtrl.create({
			title: 'Local de Entrega',
			enableBackdropDismiss: false
		});
		alert.addButton({
			text: 'Domícilio',
			handler: (data: any) => {
				this.changeLocal('Domícilio');
			}
		});

		alert.addButton({
			text: 'Loja',
			handler: (data: any) => {
				this.changeLocal('Loja');
			}
		});

		alert.present();
	}

	changeLocal(local: string) {
		this.local = local;
		this.storage.get('local').then((value) => {
			if(value && value != this.local) {
				this.cleanCart();
			}
			this.storage.set('local', this.local);
			this.loadMenu(this.local);
		});
	}

	cartSegment() {
		this.segment = 'cart';
	}

	addToCart(product: any, measure: any) {
		this.cartService.addItem(product, measure).then((cart) => {
			if (typeof measure.quantity == "undefined") measure.quantity = 0;
			measure.quantity++;
			this.cart = cart;
			// const toast = this.toastCtrl.create({
			// 	message: product.name + ' adicionado ao carrinho',
			// 	duration: 1000
			// });
			// toast.present();
		});
	}

	incrementQuantity(product: any) {
		this.cartService.incrementQuantity(product).then((cart) => {
			let auxCategory = this.fixedMenu.find((p: any) => p.name === product.category); 
			let auxItem = auxCategory.items.find((p: any) => p.id === product.id);
			if(!auxItem) {
				auxCategory = this.dailyMenu.find((p: any) => p.name === product.category); 
				auxItem = auxCategory.items.find((p: any) => p.id === product.id);
			}
			if(auxItem) {
				let auxMeasure = auxItem.measures.find((m: any) => m.name === product.measure_name);
				if(auxMeasure) {
					auxMeasure.quantity = product.quantity + 1;
				}								
			}
			this.cart = cart;
		});
	}

	decrementQuantity(product: any) {
		this.cartService.decrementQuantity(product).then((cart) => {
			let auxCategory = this.fixedMenu.find((p: any) => p.name === product.category); 
			let auxItem = auxCategory.items.find((p: any) => p.id === product.id);
			if(auxItem) {
				let auxMeasure = auxItem.measures.find((m: any) => m.name === product.measure_name);
				if(auxMeasure) {
					auxMeasure.quantity = product.quantity - 1;
				}								
			}
			this.cart = cart;
		});
	}

	removeFromCart(product: any) {
		this.cartService.removeItem(product).then((cart) => {
			let auxCategory = this.fixedMenu.find((p: any) => p.name === product.category); 
			let auxProduct = auxCategory.items.find((p: any) => p.name === product.name);
			auxProduct.measures.forEach((measure: any) => {
				if(product.measure_name == measure.name) measure.quantity = 0;
			})

			auxCategory = this.dailyMenu.find((p: any) => p.name === product.category); 
			if(auxCategory) {
				auxProduct = auxCategory.items.find((p: any) => p.name === product.name);
				auxProduct.measures.forEach((measure: any) => {
					if(product.measure_name == measure.name) measure.quantity = 0;
				})
			}
			this.cart = cart;
			const toast = this.toastCtrl.create({
				message: product.name + ' removido do carrinho',
				duration: 1000
			});
			toast.present();
		});
	}

	cleanCart() {
		this.cart = new Cart();
		this.cartService.set(this.cart);
		this.fixedMenu.forEach((category: any) => {
			category.items.forEach((item: any) => {
				item.measures.forEach((measure: any) => {
					measure.quantity = 0;
				})
			})
		})
		this.dailyMenu.forEach((category: any) => {
			category.items.forEach((item: any) => {
				item.measures.forEach((measure: any) => {
					measure.quantity = 0;
				})
			})
		})
	}

	loadMenu(local: string) {
		this.loading = this.loadingCtrl.create();
		this.loading.present().then(()=>{
			this.productService.getFixed(local)
			.then(data => {
				this.fixedMenu = this.addMeasures(data);
				this.productService.getDaily(local)
				.then(data => {
					this.dailyMenu = this.addMeasures(data);
					this.loading.dismiss();
				}, error => {
					this.alert("Erro", "Verifique a sua ligação à rede e tente novamente.");
					this.loading.dismiss();
				});
			}, error => {
				this.alert("Erro", "Verifique a sua ligação à rede e tente novamente.");
				this.loading.dismiss();
			});
		});
	}

	addMeasures(menu: any) : any {
		let aux = menu;
		aux.forEach((category: any) => {
			category.items.forEach((item: any) => {
				item.measures.forEach((measure: any) => {
					let auxItem = this.cart.items.find((p: any) => p.id === item.id && p.measure_name === measure.name);
					if(auxItem) measure.quantity = auxItem.quantity;
				})
			})
		})
		return aux;
	}

	presentFilter() {
		let modal = this.modalCtrl.create('MenuFilterPage', { fixedMenu: this.fixedMenu, excludeCategories: this.excludeCategories });
		modal.present();

		modal.onWillDismiss((data: any[]) => {
			if (data) {
				this.excludeCategories = data;
				this.applyFilter();
			}
		});

	}

	applyFilter() {
		this.fixedMenu.forEach((fixed: any) => {
			fixed.hide = false;
			this.excludeCategories.forEach((category: any) => {
				if(fixed.name == category) {
					fixed.hide = true;
				}
			})
		})
		this.dailyMenu.forEach((daily: any) => {
			daily.hide = false;
			this.excludeCategories.forEach((category: any) => {
				if(daily.name == category) {
					daily.hide = true;
				}
			})
		})
	}

	goToCheckoutPage() {
		this.loading = this.loadingCtrl.create();
		this.loading.present().then(()=>{
			if(this.cart.totalPrice > 0) {
				this.user.hasLoggedIn().then((data) => {
					if(data) {
						this.settingsService.getOpeningTimes().then((data) => {
							if(data) {
								this.loading.dismiss();
								this.navCtrl.push('CheckoutPage', {
									cart: this.cart,
									local: this.local,
									openingTimes: data
								});
							}
							else {
								this.loading.dismiss();
								this.alert("Atenção", "O estabelecimento hoje encontra-se encerrado.");
							}
						}, (error) => {
							this.loading.dismiss();
						});
					}
					else {
						let alert = this.alertCtrl.create({
							title: 'Necessário login',
							message: 'Faça login ou registe-se para prosseguir'
						});
						alert.addButton({
							text: 'Login',
							handler: (data: any) => {
								this.navCtrl.push('LoginPage');
							}
						});

						alert.addButton({
							text: 'Registo',
							handler: (data: any) => {
								this.navCtrl.push('SignupPage');
							}
						});

						this.loading.dismiss();
						alert.present();
					}
				})
			}
		})
	}

	doRefresh(refresher: Refresher) {
		this.productService.getFixed(this.local)
		.then(data => {
			this.fixedMenu = this.addMeasures(data);
			this.productService.getDaily(this.local)
			.then(data => {
				this.dailyMenu = this.addMeasures(data);
				refresher.complete();
			}, error => {
				this.alert("Erro", "Verifique a sua ligação à rede e tente novamente.");
				refresher.complete();
			});
		}, error => {
			this.alert("Erro", "Verifique a sua ligação à rede e tente novamente.");
			refresher.complete();
		});
	}

	alert(title: string, subtitle?: string) {
		let alert = this.alertCtrl.create({
			title: title,
			subTitle: subtitle,
			buttons: ['Ok']
		});
		alert.present();
	}
}



