import { Injectable } from '@angular/core';

import { Http, Headers, RequestOptions } from '@angular/http';

import { UserService } from './user-service';

import 'rxjs/add/operator/map';
import 'rxjs/add/observable/of';

declare var google: any;

@Injectable()
export class SettingsService {
	// API_URL = 'http://172.24.62.104/link.eddmi.com/index.php/takeaway/';
	API_URL = 'https://link.eddmi.com/takeaway/';
	ENTITY_ID = 227;
	about: any;
	path: string;
	constructor(public http: Http, public userService: UserService) {
		// console.log('Hello SettingsService Provider');
	}

	get(): Promise<any> {
		if (this.about) {
			return Promise.resolve(this.about);
		}
		return new Promise((resolve, reject) => {
			let _headers = new Headers({ 'Authorization': this.ENTITY_ID });
			let _options = new RequestOptions({headers: _headers});
			let url = this.API_URL + "settings/one";
			this.http.get(url, _options)
			.map(res => res.json())
			.subscribe(data => {
				this.about = data.data.settings;
				// console.log(this.about);
				resolve(this.about);
			}, error => {
				reject(error);
			});
		});
	}  

	getPath(): Promise<any> {
		if (this.path) {
			return Promise.resolve(this.path);
		}
		return new Promise((resolve, reject) => {
			let _headers = new Headers({ 'Authorization': this.ENTITY_ID });
			let _options = new RequestOptions({headers: _headers});
			let url = this.API_URL + "settings/path/";
			this.http.get(url, _options)
			.map(res => res.json())
			.subscribe(data => {
				this.path = data.data.settings.path;
				resolve(this.path);
			}, error => {
				reject(error);
			});
		});
	}   

	getFixedFee(): Promise<any> {
		if (this.path) {
			return Promise.resolve(this.path);
		}
		return new Promise((resolve, reject) => {
			this.userService.getToken().then(data => {
                let _headers = new Headers({ 'Authorization': this.ENTITY_ID });
				let _options = new RequestOptions({headers: _headers});
				let url = this.API_URL + "settings/fixedFee/";
				this.http.get(url, _options)
				.map(res => res.json())
				.subscribe(data => {
					let fee = data.data.fee;
					resolve(fee);
				}, error => {
					reject(error);
				});
			});	
		});
	}  

	getFees(): Promise<any> {
		if (this.path) {
			return Promise.resolve(this.path);
		}
		return new Promise((resolve, reject) => {
			this.userService.getToken().then(data => {
                let _headers = new Headers({ 'Authorization': this.ENTITY_ID });
				let _options = new RequestOptions({headers: _headers});
				let url = this.API_URL + "settings/fees/";
				this.http.get(url, _options)
				.map(res => res.json())
				.subscribe(data => {
					let fees = data.data.fees;
					resolve(fees);
				}, error => {
					reject(error);
				});
			});	
		});
	}   

	getOpeningTimes(): Promise<any> {
		if (this.path) {
			return Promise.resolve(this.path);
		}
		return new Promise((resolve, reject) => {
			this.userService.getToken().then(data => {
                let _headers = new Headers({ 'Authorization': this.ENTITY_ID });
				let _options = new RequestOptions({headers: _headers});
				let url = this.API_URL + "settings/schedule/";
				this.http.get(url, _options)
				.map(res => res.json())
				.subscribe(data => {
					let opening_times = data.data;
					if(data.data) {
						opening_times = data.data.opening_times;
					}
					resolve(opening_times);
				}, error => {
					reject(error);
				});
			});	
		});
	}   

	computeTotalDistance(result: any): number {
        var total = 0;
        var myroute = result.routes[0];
        for (var i = 0; i < myroute.legs.length; i++) {
            total += myroute.legs[i].distance.value;
        }
        total = total / 1000;
        return total;
    }


	getDistance(address: string): Promise<any> {
		return new Promise((resolve, reject) => {
			this.getPath().then(data => {
				let decodedPath = google.maps.geometry.encoding.decodePath(data);
				let qt = {lat:  41.3354534, lng: -8.5601993};
				let response = {
					status: "",
					valid: "",
					distance: 0,
					address: "",
					locality: ""
				}
				let geocoder = new google.maps.Geocoder();

				geocoder.geocode( { 'address': address }, function(results: any, status: any) {
					if (status == google.maps.GeocoderStatus.OK) {
						var request = {
							destination: results[0].geometry.location,
							origin: qt,
							travelMode: google.maps.TravelMode.DRIVING
						};

						var locality = results[0].address_components[1].long_name;
						var address = results[0].formatted_address;
								
						var directionsService = new google.maps.DirectionsService();
						directionsService.route(request, function(result: any, status: any) {
							if (status == google.maps.DirectionsStatus.OK) {
								var polygon = new google.maps.Polygon({ paths: decodedPath });
								response.valid = google.maps.geometry.poly.containsLocation(results[0].geometry.location, polygon);
								if(response.valid) {
									for (var i = 0; i < result.routes[0].legs.length; i++) {
										response.distance +=  result.routes[0].legs[i].distance.value;
									}
									response.distance = response.distance / 1000;
								}
								response.status = "success";
								response.address = address;
								response.locality = locality;
								// console.log(response.distance, response.address);
								resolve(response);
							}
							else {
								response.status = "error";
								reject(response);                
							}
						});
						
					} else {
						response.status = "error";
						reject(response);
					}
				});
			})
    	})
	};
}



